@media screen and (max-width: 629px){
    .contactContainer{
        height: max-content;
    }
    .inputForm{
        width: 100%;
    }
    .contactData{
        width: 100%;
    }

}
@media screen and (min-width: 630px){
    .contactContainer{
        height: 100vh;
    }
    .inputForm{
        width: 60%;
    }
    .contactData{
        width: 40%;
    }
}
.contactContainer{
    background-color: cornflowerblue;
}
.inputForm{
    background-color: mediumpurple;
    border: 1px solid;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.form{
    border: 1px solid;
    width: 100%;
    display: flex;
    flex-direction: column;
}

.contactData{
    border: 1px solid;
    background-color: green;
}
.contactData div{
    border: 1px solid;
    background-color: yellow;
}